.navbar {
  height: 65px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
}

.navbar div {
  display: flex;
}

.navbar .title {
  margin-left: 20px;
  font-size: 24px;
}

.sidebar {
  width: 200px;
  height: 100%;
  position: fixed;
  top: 0;
  left: -250px;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
  background: #fff;
  padding: 1rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.sidebar .closeBtn {
  display: flex;
  flex-direction: row-reverse;
}

.sidebar.open {
  -webkit-transform: translateX(250px);
  transform: translateX(250px);
}

.sidebar > .title {
  margin-top: 30px;
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 500;
}

.sidebar > .post {
  font-size: 18px;
  margin-bottom: 5px;
}
